import React, { Component } from 'react';
import { Tree, Breadcrumb } from 'antd';
import { CatalogTitle } from 'components';

import './index.less';

const { DirectoryTree, TreeNode } = Tree;

const treeData = [
  {
    title: 'parent 0',
    key: '0-0',
    children: [
      { title: 'leaf 0-0', key: '0-0-0', isLeaf: true },
      { title: 'leaf 0-1', key: '0-0-1', isLeaf: true }
    ]
  },
  {
    title: 'parent 1',
    key: '0-1',
    children: [
      { title: 'leaf 1-0', key: '0-1-0', isLeaf: true },
      { title: 'leaf 1-1', key: '0-1-1', isLeaf: true }
    ]
  }
];
class Index extends Component {
  render() {
    return (
      <div className="detail">
        <div className="aside">
          <div className="notes-catalog">
            <div className="notes-title">我就是目录</div>
            <DirectoryTree draggable switcherIcon={null} icon={null} defaultExpandAll>
              {treeData.map(i => (
                <TreeNode key={JSON.stringify(i)} title={<CatalogTitle visible title={i.title} />}>
                  {i.children.map(x => (
                    <TreeNode key={JSON.stringify(x)} title={<CatalogTitle visible read title={x.title} />} />
                  ))}
                </TreeNode>
              ))}
            </DirectoryTree>
          </div>
        </div>
        <div className="detail-content">
          <div className="breadcrumb">
            <Breadcrumb>
              <Breadcrumb.Item>第一级目录</Breadcrumb.Item>
              <Breadcrumb.Item>我是第二级</Breadcrumb.Item>
            </Breadcrumb>
          </div>
          <div className="article">
            <div className="article-content">没错我是富文本内推</div>
            <div className="page">
              <div className="page-item prev">
                <div className="name">上一篇</div>
                <div className="title">xxx</div>
              </div>
              <div className="page-item next">
                <div className="name">下一篇</div>
                <div className="title">xxx</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Index;
